import appCashModel from "../../../../components/page-component/app-cash-model/app-cash-model";

export default {
    name: "cash",
    components: {appCashModel},
    data() {
        return {
            cashTypeModel: false,
            is_wx: false,
            is_alipay: false,
            is_bank: false,
            is_balance: false,
            is_auto: false,
            form: {
                cash_money: '',
                account: '',
                nickname: '',
                bank_name: '',
            },
            mch_id: 0,
            pay_type: '',
            account_money: '',
            setting: null,
            template_message_list: null,
        }
    },
    computed: {
        cashName() {
            switch (this.pay_type) {
                case 'auto':

                    return '微信零钱';







                case 'wx':
                    return '微信线下打款';
                case 'alipay':
                    return '支付宝线下打款';
                case 'bank':
                    return '银联线下打款';
                case 'balance':
                    return '商城余额';
                default:
                    return;
                    break;
            }
        },
    },
    onLoad(options) { this.$commonLoad.onload(options);
        const self = this;
        self.mch_id = options.mch_id;
        self.account_money = options.account_money;

        self.$showLoading({text: '正在提交'});
        self.$request({
            url: self.$api.mch.setting,
            data: {
                mch_id: self.mch_id
            }
        }).then(info => {
            self.$hideLoading();
            if (info.code === 0) {
                let setting = info.data.setting;
                [
                    self.setting,
                    self.is_wx,
                    self.is_alipay,
                    self.is_bank,
                    self.is_balance,
                    self.is_auto,
                    self.template_message_list
                ] = [
                    setting,
                    setting.cash_type.indexOf('wx') !== -1,
                    setting.cash_type.indexOf('alipay') !== -1,
                    setting.cash_type.indexOf('bank') !== -1,
                    setting.cash_type.indexOf('balance') !== -1,
                    setting.cash_type.indexOf('auto') !== -1,
                    info.data.template_message_list,
                ];
            }
        }).catch(e => {
            self.$hideLoading();
        })
    },
    methods: {
        payTypeChange(value) {
            this.pay_type = value;
        },

        subscribe(callback) {
            this.$subscribe(this.template_message_list).then(res => {
                callback();
            }).catch(res => {
                callback();
            });
        },
        submit: function (e) {
            const self = this;
            let form = self.form;
            let pay_type = self.pay_type;
            let cash_max = self.account_money;

            let content = function () {
                if (!form.cash_money) {
                    return "请输入提现金额";
                }
                let cash = parseFloat(parseFloat(form.cash_money).toFixed(2));

                if (cash > cash_max) {
                    return "提现金额不能超过" + cash_max + "元";
                }
                if (cash < 1) {
                    return "提现金额不能低于1元";
                }
                if (!pay_type) {
                    return "请选择提现方式";
                }
                if (pay_type === 'wx' || pay_type === 'alipay' || pay_type === 'bank') {
                    if (!form.nickname) {
                        return "姓名不能为空";
                    }
                    if (!form.account) {
                        return "账号不能为空";
                    }
                }
                if (pay_type === 'bank' && !form.bank_name) {
                    return "开户行不能为空";
                }
                if (pay_type === 'balance' || pay_type === 'auto') {
                }
                return false;
            }();

            if (content) {
                uni.showToast({icon: 'none', title: content});
                return
            }
            self.subscribe(() => {
                self.$showLoading({text: '正在提交'});
                self.$request({
                    url: self.$api.mch.cash_submit,
                    method: 'POST',
                    data: {
                        mch_id: self.mch_id,
                        money: form.cash_money,
                        type: self.pay_type,
                        type_data: JSON.stringify(form),
                    }
                }).then(info => {
                    self.$hideLoading();
                    uni.showModal({
                        title: "提示",
                        content: info.msg,
                        showCancel: false,
                        success: function (e) {
                            if (e.confirm) {
                                if (info.code === 0) {
                                    uni.redirectTo({url: '/plugins/mch/mch/cash-log/cash-log?mch_id=' + self.mch_id})
                                }
                            }
                        }
                    });
                }).catch(e => {
                    self.$hideLoading();
                })
            })
        }
    }
}
